<template>
    <div id="myTeacherCourse">
        <el-table
            :data="listData"
            stripe
            style="width: 100%">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <div class="course_detail" v-for="(detailItem, detailIndex) in JSON.parse(props.row.course_detail)" :key="detailIndex">
                        {{ detailItem.name }}
                        <p class="primaryAcce"><a :href="detailItem.accessory_address[0]" download>{{ detailItem.accessory[0] }}</a></p>
                        <div class="classButtons">
                            <el-button type="primary" class="allButton" size="mini" @click="showAllAccessory(props, props.row.course_id, detailIndex)">全部附件</el-button>
                            <el-button type="primary" class="uploadButton" size="mini" @click="showUploadDialog(props, props.row.course_id, detailIndex)">上传附件</el-button>
                            <el-button type="primary" class="homeworkButton" size="mini" @click="showHomeworkDialog(props, props.row.course_id, detailIndex)">学生作业</el-button>
                            <el-button type="primary" icon="el-icon-edit" size="mini"  @click="showEditDialog(props, props.row.course_id, detailIndex)"></el-button>
                            <el-button type="danger" icon="el-icon-delete" size="mini"  @click="alertDelete(props, props.row.course_id, detailIndex)"></el-button>
                        </div>
                    </div>
                    <div class="addNew">
                        <el-button class="addNewButton" @click="showAddTaskDialog(props)">添加新实验</el-button>
                    </div>
                </template>
            </el-table-column>
            <el-table-column
                prop="course_id"
                label="课程编号"
                width="180">
            </el-table-column>
            <el-table-column
                prop="course_name"
                label="课程名称"
                width="180">
            </el-table-column>
            <el-table-column
                prop="course_teacher"
                label="任教老师">
            </el-table-column>
            <el-table-column
                width="200"
                label="操作">
                <template slot-scope="scope">
                    <el-button @click="showStudentsDialog(scope.row)" size="small">学生名单</el-button>
                    <el-button type="danger" @click="deleteCourse(scope.row)" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="totalCourse">
            </el-pagination>
        </div>
        <el-dialog title="附件" :visible.sync="dialogAccessoryVisible" label-width="100px" label-position="left">
            <el-tag
                v-for="(acceItem, acceIndex) in accessoryData.accessory"
                :key="acceIndex"
                closable
                @close="deleteOneAcce(accessoryData.accessory_address[acceIndex], acceIndex)"
                style="margin-right: 20px">
                <a :href="accessoryData.accessory_address[acceIndex]" download class="downloadAcceButton">{{ acceItem }}</a>
            </el-tag>
        </el-dialog>
        <el-dialog title="上传附件" :visible.sync="dialogUploadVisible" label-width="100px" label-position="left">
            <p class="newTaskp">添加附件：</p>
            <el-upload
                class="upload-demo"
                ref="upload"
                multiple
                :action="uploadUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="uploadFileList"
                :with-credentials="true"
                :limit="10"
                :before-upload="beforeUploadFileHandler"
                :on-success="onSuccessFileHandler"
                :data="uploadExtraData"
                :auto-upload="false">
                <el-button style="margin-left: 10px;" slot="trigger" size="small" type="primary">选取</el-button>
                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传</el-button>
            </el-upload>
        </el-dialog>
        <el-dialog title="编辑详情" :visible.sync="dialogEditVisible" label-width="100px" label-position="left">
            <p class="newTaskp">实验名称：</p>
            <el-input
                placeholder="请输入实验名称"
                v-model="editTaskName"
                clearable>
            </el-input>
            <p class="newTaskp">截止日期：</p>
            <el-date-picker
                v-model="editDeadlineTime"
                type="datetime"
                placeholder="选择日期">
            </el-date-picker>
            <p class="comfirmEditTask">
                <el-button @click="dialogEditVisible = false">取消</el-button>
                <el-button classsize="small" type="primary" @click="submitEditTask">更改</el-button>
            </p>
        </el-dialog>
        <el-dialog title="学生作业" :visible.sync="dialogHomeworkVisible" label-width="100px" label-position="left">
            
        </el-dialog>
        <el-dialog title="添加新实验" :visible.sync="dialogAddTaskVisible">
            <p class="newTaskp">实验名称：</p>
            <el-input
                placeholder="请输入实验名称"
                v-model="taskName"
                clearable>
            </el-input>
            <p class="newTaskp">截止日期：</p>
            <el-date-picker
                v-model="deadlineTime"
                type="datetime"
                placeholder="选择日期">
            </el-date-picker>
            <p class="comfirmAddNewTask"><el-button classsize="small" type="success" @click="submitAddNewTask">确定添加</el-button></p>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'MyTeacherCourse',
    props: {
        msg: String
    },
    data: function() {
        return {
            dialogAccessoryVisible: false,
            dialogUploadVisible: false,
            dialogHomeworkVisible: false,
            dialogEditVisible: false,
            dialogAddTaskVisible: false,
            listData: [],
            primaryAcces: [],
            accessoryData: [],
            homeworkData: [],
            uploadFileList: [],
            uploadExtraData: {
                course_id: '',
                name: '',
                index: '',
                action: 'edit'
            },
            uploadUrl: '/api/research/operateTask',
            httpAddress: '/api',
            currentPage: 0,
            page: 0,
            totalPage: 0,
            totalCourse: 0,
            deadlineTime: '',
            taskName: '',
            detailIndex: '',
            editTaskName: '',
            editDeadlineTime: '',
        }
    },
    mounted: function() {
        this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
            .then((res) => {
                // console.log('res', res);
                let data = res.data;
                if (data.status == 0 && data.msg == 'ok') {
                    this.listData = data.data.data;
                    this.totalCourse = data.data.total;
                    this.totalPage = Math.ceil(data.data.total / 10);
                }
            })
            .catch((err) => {
                console.log('err', err);
            })
    },
    methods: {
        showStudentsDialog(row) { // 展示学生名单
            console.log(row);
        },
        showAllAccessory(props, course_id, detailIndex) { // 展示全部附件
            let detail = JSON.parse(props.row.course_detail)[detailIndex];
            this.detailIndex = detailIndex;
            // let address = detail.accessory.map(item => `/accessory/${course_id}/${JSON.parse(props.row.course_detail)[detailIndex].name}/${item}`);
            this.accessoryData = {
                accessory: detail.accessory,
                accessory_address: detail.accessory_address
            };
            this.dialogAccessoryVisible = true;
        },
        showUploadDialog(props, course_id, detailIndex) { // 上传附件
            this.uploadExtraData.course_id = course_id;
            this.uploadExtraData.name = JSON.parse(props.row.course_detail)[detailIndex].name;
            this.uploadExtraData.index = detailIndex;
            console.log(this.uploadExtraData);
            this.dialogUploadVisible = true;
        },
        showHomeworkDialog(props, course_id, detailIndex) { // 展示学生作业列表
            console.log(props, course_id, detailIndex);
            // this.$http.get(`${this.httpAddress}/research/getMyHomework`, {
            //     params: {
            //         course_id: course_id,
            //         index: detailIndex,
            //         name: JSON.parse(props.row.course_detail)[detailIndex].name
            //     }
            // }).then(res => {
            //     let data = res.data;
            //     if (data.status == 0 && data.msg == 'ok') {
            //         let realData = data.data;
            //         let address = realData.filename.map(item => `/homework/${course_id}/${JSON.parse(props.row.course_detail)[detailIndex].name}/${realData.username}/${item}`);
            //         this.homeworkData = {
            //             homework: realData.filename,
            //             homework_address: address
            //         };
            //         this.dialogHomeworkVisible = true;
            //     } else {
            //         this.$message({
            //             message: '你还未上传作业',
            //             type: 'warning'
            //         })
            //     }
            // }).catch(err => {
            //     console.log(err);
            // })
        },
        showEditDialog(props, course_id, detailIndex) { // 编辑实验详情
            let detail = JSON.parse(props.row.course_detail);
            this.editTaskName = detail[detailIndex].name;
            let deadline = detail[detailIndex].deadline;
            let datetime = new Date(parseInt(deadline));
            this.editDeadlineTime = new Date(datetime.getFullYear(), datetime.getMonth(), datetime.getDate(), datetime.getHours(), datetime.getMinutes(), datetime.getSeconds());
            this.dialogEditVisible = true;
        },
        alertDelete(props, course_id, detailIndex) { // 删除实验任务
             this.$confirm('此操作将永久删除该实验任务, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http.post(`${this.httpAddress}/research/operateTask`, 
                        {
                            course_id: course_id,
                            action: "delete",
                            task: {
                                name: JSON.parse(props.row.course_detail)[detailIndex].name,
                                index: detailIndex,
                            }
                        })
                        .then(res => {
                            let data = res.data;
                            if (data.status == 0 && data.msg == 'ok') {
                                this.$message({
                                    type: 'success',
                                    message: '删除成功！'
                                });
                            } else {
                                this.$message({
                                    type: 'warning',
                                    message: '删除失败！'
                                });
                            }
                        })
                        .catch(err => {
                            console.log('err', err);
                            this.$message({
                                type: 'warning',
                                message: '删除失败！'
                            });
                        })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
        },
        showAddTaskDialog(props) { // 添加实验任务
            this.uploadExtraData.course_id = props.row.course_id;
            this.uploadUrl =`${this.httpAddress}/research/operateTask`;
            this.dialogAddTaskVisible = true;
        },
        submitAddNewTask() { // 确认添加实验任务
            this.$http.post(`${this.httpAddress}/research/operateTask`, 
                {
                    course_id: this.uploadExtraData.course_id,
                    action: 'add',
                    task: {
                        name: this.taskName,
                        deadline: this.deadlineTime.getTime(),
                    }   
                })
                .then(res => {
                    let data = res.data;
                    if (data.status == '0' && data.msg == 'ok') {
                        this.dialogAddTaskVisible = false;
                        this.$message({
                            type: 'success',
                            message: '添加实验任务成功！'
                        })
                        this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
                            .then((res) => {
                                // console.log('res', res);
                                let data = res.data;
                                if (data.status == 0 && data.msg == 'ok') {
                                    this.listData = data.data.data;
                                    this.totalCourse = data.data.total;
                                    this.totalPage = Math.ceil(data.data.total / 10);
                                }
                            })
                            .catch((err) => {
                                console.log('err', err);
                            })
                    } else {
                        this.$message({
                            type: 'warning',
                            message: '添加失败！'
                        });
                    }
                })
                .catch(err => {
                    console.log('err', err);
                    this.$message({
                        type: 'warning',
                        message: '添加失败！'
                    });
                })
        },
        submitEditTask() {
            console.log(this.editDeadlineTime)
        },
        beforeUploadFileHandler(file) {
            console.log(file);
        },
        onSuccessFileHandler(file) {
            console.log('success',file);
        },
        submitUpload() {
            this.$refs.upload.submit();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        deleteCourse(row) {
            // console.log(row);
            this.$confirm('此操作将永久删除该实验任务, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http.get(`${this.httpAddress}/research/operateCourse`, {
                            params: {
                                action: "delete",
                                course_id: row.course_id,
                            }
                        }).then((res) => {
                            // console.log("res", res);
                            if (res.data.status == 0 && res.data.msg == 'ok') {
                                this.$message({
                                    type: 'success',
                                    message: '删除成功！'
                                });
                                this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
                                    .then((res) => {
                                        console.log('res', res);
                                        let data = res.data;
                                        if (data.status == 0 && data.msg == 'ok') {
                                            this.listData = data.data.data;
                                            this.totalPage = Math.ceil(data.data.total / 10);
                                        }
                                    })
                                    .catch((err) => {
                                        console.log('err', err);
                                    })
                            } else {
                                this.$message({
                                    message: res.data.msg,
                                    type: 'warning'
                                });
                            }
                        }).catch((err) => {
                            console.log('err', err);
                        })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });          
                });
            // this.$http.get(`${this.httpAddress}/research/operateCourse`, {
            //     params: {
            //         action: "delete",
            //         course_id: row.course_id,
            //     }
            // }).then((res) => {
            //     // console.log("res", res);
            //     if (res.data.status == 0 && res.data.msg == 'ok') {
            //         this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
            //             .then((res) => {
            //                 console.log('res', res);
            //                 let data = res.data;
            //                 if (data.status == 0 && data.msg == 'ok') {
            //                     this.listData = data.data.data;
            //                     this.totalPage = Math.ceil(data.data.total / 10);
            //                 }
            //             })
            //             .catch((err) => {
            //                 console.log('err', err);
            //             })
            //     } else {
            //         this.$message({
            //             message: res.data.msg,
            //             type: 'warning'
            //         });
            //     }
            // }).catch((err) => {
            //     console.log('err', err);
            // })
        },
        deleteOneAcce(data, acceIndex) {
            // console.log(data, this.detailIndex);
            let result = data.split('/'); 
            console.log(result, acceIndex);

            this.$http.post(`${this.httpAddress}/research/operateTask`, 
                {
                    course_id: result[2],
                    action: 'delete',
                    task: {
                        name: result[3],
                        deleteAcce: 'true',
                        accessory: result[4],
                        acce_index: acceIndex,
                        index: this.detailIndex
                    }   
                })
                .then(res => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.accessoryData.accessory.splice(acceIndex, 1);
                        this.accessoryData.accessory_address.splice(acceIndex, 1);
                        this.$message({
                            type: 'success',
                            message: '删除附件成功！'
                        })
                    } else {
                        this.$message({
                            type: 'warning',
                            message: '删除附件失败！'
                        })
                    }
                })
                .catch(err => {
                    console.log('err', err);
                    this.$message({
                        type: 'warning',
                        message: '删除附件失败！'
                    })
                })
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.page = val - 1;
            this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
                .then((res) => {
                    // console.log('res', res);
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.listData = data.data.data;
                        this.totalCourse = data.data.total;
                        this.totalPage = Math.ceil(data.data.total / 10);
                    }
                })
                .catch((err) => {
                    console.log('err', err);
                })
        },
    }
}
</script>

<style scoped>
.downloadAcceButton {
    margin-right: 20px;
    color: #409EFF;
}
.downloadHomeworkButton {
    margin-right: 20px;
    color: #409EFF;
}
.course_detail {
    position: relative;
    line-height: 40px;
}
.primaryAcce {
    display: inline-block;
    margin-left: 20px;
    width: 300px;
}
.classButtons {
    position: absolute;
    right: 0;
    line-height: 40px;
    top: 0;
}
.allButton {
    display: inline-block;
}
.homewokButton {
    display: inline-block;
}
.uploadButton {
    display: inline-block;
}
.addNew {
    text-align: right;
    margin-top: 20px;
}
.newTaskp {
    margin: 10px 0;
}
.comfirmAdd {
    text-align: center;
}
.comfirmAddNewTask, .comfirmEditTask {
    text-align: center;
    margin-top: 20px;
}
</style>
<style>
#myTeacherCourse .el-form-item {
    margin-bottom: 10px;
    border-bottom: 1px solid #aaaaaa;
}
#myTeacherCourse .el-form-item:last-child {
    margin-bottom: 0;
}
#myTeacherCourse .el-table__expanded-cell[class*=cell] {
    padding: 20px;
}
#myTeacherCourse .pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
</style>
