<template>
    <el-container>
        <el-header height="100px">
            <span class="title">实验中心教学管理网站</span>
            <span class="login" :class="{logined: isLogined}" @click="login">登录</span>
            <span class="username">{{ username }}</span>
            <span class="logout" :class="{canlogout: canLogout}" @click="logout">退出</span>
        </el-header>
        <el-container>
            <el-aside class='menu' width="230px">
                <el-menu
                    default-active="allCourse"
                    class="real_menu"
                    
                    background-color="#409EFF"
                    text-color="#FFFFFF"
                    active-text-color="#ffd04b"
                    router
                >
                    <el-menu-item v-for="(item) in menuItem" :index="item.path" :key="item.path">
                        <span slot="title">{{ item.name }}</span>
                    </el-menu-item>
                </el-menu>
            </el-aside>
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
        <el-dialog title="登录" :visible.sync="dialogLoginVisible" label-width="100px" label-position="left">
            <el-form :model="form">
                <el-form-item label="账号">
                   <el-input v-model="form.username" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input v-model="form.password" autocomplete="off" type="password"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="loginLab()">登录</el-button>
                <el-button @click="forgetPassword()">忘记密码</el-button>
            </div>
        </el-dialog>
        
    </el-container>
</template>

<script>
export default {
    name: 'Index',
    data: function () {
        return {
            username: '',
            isLogined: false,
            canLogout: false,
            form: {
                username: '',
                password: '',
            },
            menuItem: [
                {
                    name: '全部课程',
                    path: 'allCourse'
                },
                {
                    name: '资源中心',
                    path: 'resourceCenter'
                },
            ],
            dialogLoginVisible: false,
            httpAddress: '/api',
        }
    },
    mounted: function () {
        // this.$http.get(`${this.httpAddress}/public.zip`)
        // .then(res => {
        //     console.log(res);
        // })
        // .catch(err => {
        //     console.log(err);
        // })
        this.$http.get(`${this.httpAddress}/research/checkLogin`)
            .then((res) => {
                // console.log('res', res);
                let data = res.data;
                if (data.status == 0 && data.msg == 'ok') {
                    if (data.data.identity == '1') {
                        this.username = `${data.data.name}同学，你好`;
                        this.$root.$emit('studentLogin');
                        this.menuItem.push({
                            name: '我的课程',
                            path: 'myStudentCourse'
                        })
                    } else if (data.data.identity == '2') {
                        this.username = `${data.data.name}老师，您好`;
                        this.$root.$emit('teacherLogin');
                        this.menuItem.push({
                            name: '我的课程',
                            path: 'myTeacherCourse'
                        })
                    }
                    this.isLogined = true;
                    this.dialogLoginVisible = false;
                    this.canLogout = true;
                }
            }).catch((err) => {
                console.log('err', err);
            })
    },
    methods: {
        login() {
            this.dialogLoginVisible = true;
        },
        loginLab() {
            // this.$http.post('http://localhost:3000/research/login', this.form)
            // this.$http.get('http://localhost:3000/research/login')
            this.$http.post(`${this.httpAddress}/research/login`, this.form)
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        if (data.data.identity == '1') {
                            this.username = `${data.data.name}同学，你好`;
                            this.$root.$emit('studentLogin');
                            this.menuItem.push({
                                name: '我的课程',
                                path: 'myStudentCourse'
                            })
                        } else if (data.data.identity == '2') {
                            this.username = `${data.data.name}老师，您好`;
                            this.$root.$emit('teacherLogin');
                            this.menuItem.push({
                                name: '我的课程',
                                path: 'myTeacherCourse'
                            })
                        } else if (data.data.identity == '3') {
                            this.username = `${data.data.name}，您好`;
                            this.$root.$emit('teacherLogin');
                            this.menuItem.push({
                                name: '学期设置',
                                path: 'myTeacherCourse'
                            })
                        }
                        this.isLogined = true;
                        this.dialogLoginVisible = false;
                        this.canLogout = true;
                        this.$message({
                            message: '登陆成功',
                            type: 'success'
                        })
                    } else {
                        this.$message({
                            message: data.msg,
                            type: 'warning'
                        })
                    }
                    
                }).catch((err) => {
                    console.log('err', err);
                })
        },
        forgetPassword() {

        },
        logout() {
            this.$http.get(`${this.httpAddress}/research/logout`)
                .then((res) => {
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.username = ``;
                        this.isLogined = false;
                        this.canLogout = false;
                        this.menuItem.splice(this.menuItem.length - 1, 1);
                        this.$router.push({ path: 'allCourse' });
                        this.$message({
                            message: '退出成功',
                            type: 'success'
                        })
                        this.$root.$emit('logout');
                    } else {
                        this.$message({
                            message: data.msg,
                            type: 'warning'
                        })
                    }
                }).catch((err) => {
                    console.log('err', err);
                })
        },
    }
}
</script>

<style scoped>
.el-header {
    position: relative;
    background: #409EFF;
    line-height: 100px;
    color: #ffffff;
}
.title {
    font-size: 24px;
}
.login {
    position: absolute;
    cursor: pointer;
    right: 20px;
}
.logout {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    right: 20px;
    z-index: -1;
}
.logined {
    display: none;
}
.canlogout {
    z-index: 2;
    opacity: 1;
}
.username {
    position: absolute;
    top: 0;
    cursor: pointer;
    right: 70px;
}
.menu {
    overflow: hidden;
    text-align: center;
    background: #409EFF;
}
.real_menu {
    padding-top: 20px;
}
.el-menu-item span {
    font-size: 16px;
}
.el-main {
    background: #ffffff;
    height: calc(100vh - 100px);
}
.primaryAcce {
    margin-left: 20px;
}
.dialog-footer {
    display: flex;
    justify-content: center;
}
</style>
