<template>
    <div id="allCourse">
        <el-input
            placeholder="请输入搜索关键字"
            v-model="searchKeyword"
            clearable
            width="300"
            @input="searchKeywordChange">
        </el-input>
        <el-select v-model="searchType" clearable placeholder="请选择搜索类型" @change="searchTypeChange">
            <el-option
                v-for="item in searchOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <el-table
            :data="listData"
            stripe
            style="width: 100%">
            <el-table-column
                prop="course_id"
                label="课程编号"
                width="180">
            </el-table-column>
            <el-table-column
                prop="course_name"
                label="课程名称"
                width="180">
            </el-table-column>
            <el-table-column
                prop="course_teacher"
                label="任教老师">
            </el-table-column>
            <el-table-column
                width="100"
                label="操作">
                <template slot-scope="scope">
                    <el-button @click="addCourse(scope.row)" size="small" :disabled="isDisabled">添加</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="totalCourse">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AllCourse',
    props: {
        msg: String
    },
    data: function () {
        return {
            listData: [],
            httpAddress: '/api',
            currentPage: 0,
            page: 0,
            totalPage: 0,
            totalCourse: 0,
            searchKeyword: '',
            searchType: '',
            searchOptions: [
                {
                    value: 'name',
                    label: '课程名',
                    key: 'name'
                },
                {
                    value: 'teacher',
                    label: '教师名',
                    key: 'teacher'
                },
            ],
            isDisabled: true,
        }
    },
    mounted: function() {
        this.$http.post(`${this.httpAddress}/research/getAllCourse`, {page: this.page})
            .then((res) => {
                // console.log('res', res);
                let data = res.data;
                if (data.status == 0 && data.msg == 'ok') {
                    this.listData = data.data.data;
                    this.totalCourse = data.data.total;
                    this.totalPage = Math.ceil(data.data.total / 10);
                }
            })
            .catch((err) => {
                console.log('err', err);
            })
        this.$root.$on('studentLogin', () => {
            this.isDisabled = false;
        })
        this.$root.$on('teacherLogin', () => {
            this.isDisabled = true;
        })
        this.$root.$on('logout', () => {
            this.isDisabled = true;
        })
    },
    methods: {
        addCourse(row) {
            // console.log(row);
            this.$http.get(`${this.httpAddress}/research/operateCourse`, {
                params: {
                    action: "add",
                    course_id: row.course_id,
                }
            }).then((res) => {
                // console.log("res", res);
                let data = res.data;
                if (data.status == 0) {
                    this.$message({
                        message: '添加成功',
                        type: 'success'
                    });
                } else {
                    this.$message({
                        message: data.msg,
                        type: 'warning'
                    });
                }
            }).catch((err) => {
                console.log('err', err);
            })
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            // console.log(`当前页: ${val}`);
            this.page = val - 1;
            this.getCourse();
        },
        searchKeywordChange() {
            this.page = 0;
            this.currentPage = 0;
            this.getCourse();
        },
        searchTypeChange() {
            this.page = 0;
            this.currentPage = 0;
            this.getCourse();
        },
        getCourse() {
            if (this.searchKeyword && (this.searchType == 'teacher' || this.searchType == 'name')) {
                this.$http.post(`${this.httpAddress}/research/searchCourse`, {
                        keyword: this.searchKeyword,
                        type: this.searchType,
                        page: this.page
                    })
                    .then((res) => {
                        // console.log('res', res);
                        let data = res.data;
                        if (data.status == 0 && data.msg == 'ok') {
                            this.listData = data.data.data;
                            this.totalCourse = data.data.total;
                            this.totalPage = Math.ceil(data.data.total / 10);
                        }
                    })
                    .catch((err) => {
                        console.log('err', err);
                    })
            } else if (this.searchKeyword == '' && this.searchType == '') {
                this.$http.post(`${this.httpAddress}/research/getAllCourse`, {page: this.page})
                    .then((res) => {
                        // console.log('res', res);
                        let data = res.data;
                        if (data.status == 0 && data.msg == 'ok') {
                            this.listData = data.data.data;
                            this.totalCourse = data.data.total;
                            this.totalPage = Math.ceil(data.data.total / 10);
                        }
                    })
                    .catch((err) => {
                        console.log('err', err);
                    })
            }
        },
        changeDisabled() {
            this.isDisabled = true;
        }
    }
}
</script>

<style scoped>
.pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
.el-input {
    width: 300px;
    margin-right: 20px;
}
</style>
