<template>
    <div id="myCourse">
        <el-table
            :data="listData"
            stripe
            style="width: 100%">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <!-- <el-form label-position="left" class="demo-table-expand"> -->
                        <!-- <el-form-item > -->
                            <div class="course_detail" v-for="(detailItem, detailIndex) in JSON.parse(props.row.course_detail)" :key="detailIndex">
                                {{ detailItem.name }}
                                <p class="primaryAcce"><a :href="detailItem.accessory_address[0]" download>{{ detailItem.accessory[0] }}</a></p>
                                <div class="classButtons">
                                    <el-button type="primary" class="allButton" size="mini" @click="showAllAccessory(props, props.row.course_id, detailIndex)">全部附件</el-button>
                                    <el-button type="primary" class="homeworkButton" size="mini" @click="showAllHomework(props, props.row.course_id, detailIndex)">我的作业</el-button>
                                    <el-button type="primary" class="uploadButton" size="mini" @click="showUploadDialog(props, props.row.course_id, detailIndex)">上传作业</el-button>
                                </div>
                            </div>
                        <!-- </el-form-item> -->
                    <!-- </el-form> -->
                </template>
            </el-table-column>
            <el-table-column
                prop="course_id"
                label="课程编号"
                width="180">
            </el-table-column>
            <el-table-column
                prop="course_name"
                label="课程名称"
                width="180">
            </el-table-column>
            <el-table-column
                prop="course_teacher"
                label="任教老师">
            </el-table-column>
            <el-table-column
                width="100"
                label="操作">
                <template slot-scope="scope">
                    <el-button @click="deleteCourse(scope.row)" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="currentPage"
                :page-size="10"
                layout="prev, pager, next, jumper"
                :total="totalCourse">
            </el-pagination>
        </div>
        <el-dialog title="附件" :visible.sync="dialogAccessoryVisible" label-width="100px" label-position="left">
            <span v-for="(acceItem, acceIndex) in accessoryData.accessory" :key="acceIndex">
                <a :href="accessoryData.accessory_address[acceIndex]" download class="downloadAcceButton">{{ acceItem }}</a>
            </span>
        </el-dialog>
        <el-dialog title="我的作业" :visible.sync="dialogHomeworkVisible" label-width="100px" label-position="left">
            <span v-for="(homeworkItem, homeworkIndex) in homeworkData.homework" :key="homeworkIndex">
                <a :href="homeworkData.homework_address[homeworkIndex]" download class="downloadHomeworkButton">{{ homeworkItem }}</a>
            </span>
        </el-dialog>
        <el-dialog title="上传作业" :visible.sync="dialogUploadVisible">
            <el-upload
                class="upload-demo"
                ref="upload"
                multiple
                :action="uploadUrl"
                :on-preview="handlePreview"
                :on-remove="handleRemove"
                :file-list="uploadFileList"
                :with-credentials="true"
                :limit="10"
                :before-upload="beforeUploadFileHandler"
                :data="uploadExtraData"
                :auto-upload="false">
                <el-button style="margin-left: 10px;" slot="trigger" size="small" type="primary">选取</el-button>
                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传</el-button>
                <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
            </el-upload>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'MyStudentCourse',
    props: {
        msg: String
    },
    data: function() {
        return {
            dialogAccessoryVisible: false,
            dialogHomeworkVisible: false,
            dialogUploadVisible: false,
            listData: [],
            accessoryData: [],
            homeworkData: [],
            uploadFileList: [],
            uploadExtraData: {},
            uploadUrl: '/api/research/editHomework',
            httpAddress: '/api',
            currentPage: 0,
            page: 0,
            totalPage: 0,
            totalCourse: 0,
        }
    },
    mounted: function() {
        this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
            .then((res) => {
                // console.log('res', res);
                let data = res.data;
                if (data.status == 0 && data.msg == 'ok') {
                    this.listData = data.data.data;
                    this.totalCourse = data.data.total;
                    this.totalPage = Math.ceil(data.data.total / 10);
                }
            })
            .catch((err) => {
                console.log('err', err);
            })
    },
    methods: {
        showAllAccessory(props, course_id, detailIndex) {
            let detail = JSON.parse(props.row.course_detail)[detailIndex];
            let address = detail.accessory.map(item => `/accessory/${course_id}/${JSON.parse(props.row.course_detail)[detailIndex].name}/${item}`);
            this.accessoryData = {
                accessory: detail.accessory,
                accessory_address: address
            };
            this.dialogAccessoryVisible = true;
        },
        showAllHomework(props, course_id, detailIndex) {
            this.$http.get(`${this.httpAddress}/research/getMyHomework`, {
                params: {
                    course_id: course_id,
                    index: detailIndex,
                    name: JSON.parse(props.row.course_detail)[detailIndex].name
                }
            }).then(res => {
                let data = res.data;
                if (data.status == 0 && data.msg == 'ok') {
                    let realData = data.data;
                    let address = realData.filename.map(item => `/static/homework/${course_id}/${JSON.parse(props.row.course_detail)[detailIndex].name}/${item}`);
                    this.homeworkData = {
                        homework: realData.filename,
                        homework_address: address
                    };
                    this.dialogHomeworkVisible = true;
                } else {
                    this.$message({
                        message: '你还未上传作业',
                        type: 'warning'
                    })
                }
            }).catch(err => {
                console.log(err);
            })
        },
        showUploadDialog(props, course_id, detailIndex) {
            // this.uploadUrl = `http://upload/${this.listData[dataIndex].course_id}/${detailIndex}/学号`;
            this.uploadExtraData = {
                course_id: course_id,
                index: detailIndex,
                name: JSON.parse(props.row.course_detail)[detailIndex].name,
            }
            this.uploadUrl =`${this.httpAddress}/research/editHomework`;
            this.dialogUploadVisible = true;
        },
        beforeUploadFileHandler(file) {
            this.uploadExtraData.action = 'add';
            console.log(file);
        },
        submitUpload() {
            this.$refs.upload.submit();
        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handlePreview(file) {
            console.log(file);
        },
        deleteCourse(row) {
            // console.log(row);
            // this.$http.get('http://localhost:7001/research/operateCourse', {
            this.$http.get(`${this.httpAddress}/research/operateCourse`, {
                params: {
                    action: "delete",
                    course_id: row.course_id,
                }
            }).then((res) => {
                // console.log("res", res);
                if (res.data.status == 0 && res.data.msg == 'ok') {
                    this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
                        .then((res) => {
                            console.log('res', res);
                            let data = res.data;
                            if (data.status == 0 && data.msg == 'ok') {
                                this.listData = data.data.data;
                                this.totalPage = Math.ceil(data.data.total / 10);
                            }
                        })
                        .catch((err) => {
                            console.log('err', err);
                        })
                } else {
                    this.$message({
                        message: res.data.msg,
                        type: 'warning'
                    });
                }
            }).catch((err) => {
                console.log('err', err);
            })
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.page = val - 1;
            this.$http.post(`${this.httpAddress}/research/getMyCourse`, {page: this.page})
                .then((res) => {
                    // console.log('res', res);
                    let data = res.data;
                    if (data.status == 0 && data.msg == 'ok') {
                        this.listData = data.data.data;
                        this.totalCourse = data.data.total;
                        this.totalPage = Math.ceil(data.data.total / 10);
                    }
                })
                .catch((err) => {
                    console.log('err', err);
                })
        },
    }
}
</script>

<style scoped>
.downloadAcceButton {
    margin-right: 20px;
    color: #409EFF;
}
.downloadHomeworkButton {
    margin-right: 20px;
    color: #409EFF;
}
.course_detail {
    position: relative;
    line-height: 40px;
}
.primaryAcce {
    display: inline-block;
    margin-left: 20px;
    width: 300px;
}
.classButtons {
    position: absolute;
    right: 0;
    line-height: 40px;
    top: 0;
}
.allButton {
    display: inline-block;
}
.homewokButton {
    display: inline-block;
}
.uploadButton {
    display: inline-block;
}
</style>
<style>
#myCourse .el-form-item {
    margin-bottom: 10px;
    border-bottom: 1px solid #aaaaaa;
}
#myCourse .el-form-item:last-child {
    margin-bottom: 0;
}
.pagination {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}
</style>
